body {
  color: var(--general-color);
  font-family: Open Sans;
  font-size: 20px;
  font-weight: 400;
  line-height: 150%;
}

.container {
  max-width: 1310px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.general-title {
  color: var(--second-color);
  font-size: 100px;
  font-weight: 800;
}

.title {
  color: var(--second-color);
  font-size: 60px;
  font-weight: 600;
  line-height: 100%;
}

.button-frame {
  color: var(--general-color);
  border: 1px solid var(--general-color);
  border-radius: 4px;
  font-family: Inter;
  font-size: 12px;
  font-weight: 600;
  line-height: 20px;
}

.button-inherit {
  color: var(--button-color);
  border: 3px solid var(--button-color);
  border-radius: 6px;
  font-family: Inter;
  font-size: 26px;
  font-weight: 600;
  line-height: 38px;
}

.button-blue {
  color: var(--white-color);
  border: 3px solid var(--button-color);
  background: var(--button-color);
  border-radius: 8px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.button-grey {
  color: var(--general-color);
  background: rgba(95, 105, 128, .2);
  border: 1px solid rgba(95, 105, 128, .2);
  border-radius: 8px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.hidden {
  overflow: hidden;
}

@media screen and (max-width: 991.98px) {
  .container {
    max-width: 940px;
  }

  .general-title {
    font-size: 80px;
  }

  .title {
    font-size: 48px;
  }
}

@media screen and (max-width: 479.98px) {
  .container {
    max-width: 320px;
  }

  .general-title {
    font-size: 24px;
  }

  .title {
    font-size: 20px;
  }

  .button-inherit {
    border: 1px solid var(--button-color);
    font-size: 20px;
    line-height: 32px;
  }
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a :focus, a :active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input ::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button ::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a :visited, a :hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

:root {
  --white-color: #fdfdfd;
  --general-color: #5f6980;
  --second-color: #001648;
  --button-color: #019fab;
}

